import React,{useState} from 'react';
import {
  NavLink
} from "react-router-dom";



const Navigator = () =>{

  const [arrayRute , setRute] = useState([
    {text: 'work' , rute: '/work' , id:'1'},
    {text: 'about' , rute: '/about' , id: '2'},
    {text: 'contact' , rute: '/contact' , id: '3'},
  ])


  return(
    <div className="navigator">
      <div className="navigator__brand">
        <h1>Jorzon</h1>
      </div>
      <div className="navigator__nav">
        {
          arrayRute.map((item) => 
          <NavLink key={item.id} to={`${item.rute}`} activeClassName="active">
             {item.text}
          </NavLink>
          )
        }
      </div>
    </div>
 
  );
}

export default Navigator